input:not([type="checkbox"])
    width: 100%
    height: 2.5em
    background-color: transparent
    font-size: 1em
    color: $darkGray-2
    outline: none
    font-family: $fontDefault
    -webkit-appearance: none
    border-radius: 0

    +placeholder
        color: $midGray-1

label
    user-select: none

.form-group
    +clearfix
    margin-bottom: 1em

    .control-label
        font-size: 0.875em
        font-weight: 700
        color: $darkGray-1
        +transition(color $animationFast)
        padding-left: 1em
        position: relative
        display: block

        &:before
            content: ''
            width: 0.75em
            height: 1px
            position: absolute
            bottom: 0.25em
            border-bottom: 1px solid $midGray-1
            +transition(border-bottom-color $animationFast)

        &:before
            left: 0

    &.focus .control-label
        color: $secondaryAccent
        &:before
            border-bottom-color: $secondaryAccent

    input
        border-bottom: 1px solid $midGray-1

        +transition(border-bottom $animationFast)

        &:focus
            border-bottom: 1px solid $secondaryAccent

    &.error
        input
            border-bottom-color: $accent

        .control-label
            color: $accent
            &:before
                border-bottom-color: $accent

.form-group-content
    margin-bottom: 1.5em

    input
        height: 2em

.form-group-column
    float: left

.form-group-column-field
    width: 65%
    @media screen and (max-width: $microThreshold)
        width: 100%

.form-group-errors
    color: $accent
    padding: 0.25em 0
    font-size: 0.75em
    font-family: $fontHeading

    li
        padding: 0.25em 0

.form-group-column-help
    width: 35%
    padding: 2.75em 0 0 1.5em
    font-size: 0.75em
    color: $midGray-1

    @media screen and (max-width: $microThreshold)
        display: none

.form-group-password
    .strength-meter
        background-color: $lightGray-3
        height: 0.25em

        div
            height: 0.25em
            width: 0%
            +transition(all 150ms)

        .veryweak
            background-color: $error
        .weak
            background-color: #e67e22
        .medium
            background-color: #f1c40f
        .strong
            background-color: $success

.form-select-container
    display: inline-block
    position: relative
    border-bottom: 1px solid $midGray-1
    margin-right: 1em

    i
        +position(absolute, 0.625em 0 null null)
        color: $darkGray-1
        font-size: 0.875em
        +transition(color $animationFast)

    select
        height: 2em
        position: relative
        z-index: 1
        cursor: pointer
        background: transparent
        -webkit-appearance: none
        border: 0
        box-shadow: none
        outline: none
        padding-right: 1.5em
        font-size: 1em
        color: $darkGray-2
        padding-top: 0.25em

.form-group-icon-label

    .icon-label
        float: right
        color: $midGray-1
        display: inline-block
        width: 2em
        text-align: center
        font-size: 1.25em
        line-height: 2em
        +transition(color $animationFast)

    input
        float: right
        width: calc(100% - 2.5em)

        &:focus + .icon-label
            color: $secondaryAccent

.content-form
    padding: 6em 2em 3em

    h3
        font-size: 1.5em
        font-family: $fontHeading
        margin-bottom: 1em
        color: $midGray-3

    @media screen and (max-width: $collapseThreshold)
        padding: 5em 1.5em 2em
    @media screen and (max-width: $microThreshold)
        padding: 4em 1em 1em

.content-form-sub
    padding: 2em
    border-top: 1px solid $secondaryAccent
    margin-top: 1em

.no-touch
    .form-select-container:hover
        i
            color: $darkGray-2